<template>
	<div class="guide-container">
		<div class="guide-header">
			<div class="guide-zhong">
				<div class="guide-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="guide-ti">恋爱指南</div>
				<div></div>
			</div>
		</div>
		<div class="guide-content">
			<div v-for="item in handbook_list">
				<div class="guide-shang">{{item.handbook_title}}</div>
				<div class="guide-flex">
					<div class="guide-shi">
						<img src="../assets/icon_shijian.png">
					</div>
					<!--<div class="guide-jian">{{$root.formatDate(item.create_time)}}</div>-->
					<div class="guide-jian">{{item.create_time}}</div>
				</div>
				<div v-html="item.handbook_content" class="guide-zizhong">
					<!--{{item.handbook_content}}-->
				</div>
				<div class="guide-tu" @click="openSimpleDialog(item)">
					<!--<img src="../assets/img_lazn.png">-->
					<img :src="!!item.handbook_pic?baseUrl+item.handbook_pic:'../assets/img_lazn.png'">
				</div>
			</div>
		</div>
		<mu-dialog width="100%" :open.sync="openSimple">
			<div class="guide-lun" @click="openBotttomSheet">
		  		<mu-carousel interval='80000'>
					<mu-carousel-item v-for="(item,index) in handbook_list" :key="index">
						<img :src="baseUrl+item.handbook_pic">
					</mu-carousel-item>
				</mu-carousel>
		  	</div>
		  	<!--<div class="guide-tantu" @click="openBotttomSheet">
		  		<img :src="!!img_url?baseUrl+img_url:'../assets/img_lazn.png'">
		  	</div>-->
	    </mu-dialog>
	    <mu-bottom-sheet :open.sync="open">
	   		 <mu-list @item-click="closeBottomSheet">
	      		<mu-sub-header>
	      			<div class="guide-between">
	      				<div class="guide-flex" @click="save()">
		      				<div class="guide-xiazai">
		      					<img src="../assets/xiazai.png">
		      				</div>
		      				<div class="guide-baocun">保存</div>
		      			</div>
		      			<div class="guide-qux" @click=" closeSimpleDialog()">取消</div>
	      			</div>
	      		</mu-sub-header>
	    	</mu-list>
		</mu-bottom-sheet>
		<div class="guide-footer">

		</div>
	</div>
</template>

<script>
	import Qs from "qs"
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				data: [],
				handbook_list: '',
				openSimple: false,
				open: false,
				img_url: '',
			}
		},
		methods: {
			closeSimpleDialog () {
		      this.openSimple = false;
		      this.closeBottomSheet()
		    },
			openSimpleDialog (item) {
				let that = this
				that.img_url = item.handbook_pic;
				that.openSimple = true;
		    },
		     closeBottomSheet () {
		      this.open = false;
		    },
		    openBotttomSheet () {
		      this.open = true;
		    },
			back() {
				window.history.back();
			},
			handbook() {
				var that = this;
				var params = {
//					articleid: that.articleid
				};
				that.$http.post('/handbook/info_handbook', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.handbook_list = res.data;
					}
				})
			},
			//			保存图片
			save(img_url){
				var that = this
				dsBridge.call('saveImg',that.baseUrl+that.img_url);
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.handbook();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.guide-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.guide-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.guide-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.guide-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.guide-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.guide-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		padding-left: 1.7rem;
		padding-right: 1.7rem;
	}
	
	.guide-shang {
		font-size: 1.5rem;
		color: #000000;
		padding-top: 1rem;
		font-weight: bold;
	}
	
	.guide-flex {
		display: flex;
		padding-top: 1.5rem;
	}
	
	.guide-shi img {
		width: auto;
		height: 1.2rem;
		margin-right: 0.5rem;
	}
	
	.guide-jian {
		font-size: 1.2rem;
	}
	
	.guide-zizhong {
		font-size: 1.4rem;
		color: #000000;
		padding-top: 1rem;
	}
	
	.guide-tu img {
		width: 100%;
		height: 180px;
		padding: 1rem 3rem 1.5rem 3rem;
	}
	
	.guide-xia {
		font-size: 1.4rem;
		color: #000000;
		padding-top: 1rem;
		padding-bottom: 2rem;
	}
	.guide-tantu{
		margin-bottom: -1rem;
	}
	.guide-tantu img{
		width: 100%;
		height: auto;
		margin: 0 auto;
	}
	.guide-between{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.guide-flex{
		display: flex;
	}
	.guide-xiazai img{
		height: 2rem;
		width: auto;
		margin-top: 1rem;
		margin-right: 1rem;
	}
	.guide-baocun{
		font-size: 1.5rem;
		color: #000;
		/*text-align: center;*/
	}
	.guide-qux{
		font-size: 1.5rem;
		padding-right: 1.5rem;
		color: #000000;
		padding-top: 1rem;
	}
	/*轮播图*/
	.guide-lun{
		width: 100%;
		height: 100%;
		position: relative;
	}
	.guide-lun img {
		width: 100%;
		/*height: 100%;*/
		height: auto;
		margin: 0 auto;
	}
	/deep/ .mu-dialog-body{
		padding: 0;
	}
	/deep/ .mu-dialog{
		max-width : 100%;
		background: #000000;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	
	/deep/ .mu-carousel {
		width: 100%;
		height: 75rem;
	}
	/deep/ .mu-tab {
		min-width: 0;
	}
	
	/deep/ .mu-carousel-indicator-icon {
		background: #fe4563;
	}
	
	/deep/ .mu-carousel-button.mu-icon-button {
		font-size: 0;
		z-index: 0;
	}
</style>


